import { useEffect } from "react";

function Modal(props) {
  useEffect(() => {
    //modal     
    document.body.style.overflow = 'hidden';
    return() => {
      //          
      document.body.style.overflow = 'auto';
    }
  }, []);
	return (
		<aside className='modal'>
			<div className='con'>{props.children}</div>
			<span
				className='close'
				onClick={() => {
					props.setOpen(false);
				}}
			>
				Close
			</span>
		</aside>
	);
}

export default Modal;
